.about {
  padding-top: 6rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-height: 100vh;
  background: radial-gradient(at left top, #2373ce, #8e9eed, #2373ce);
  background-size: 400% 400%;
  -webkit-animation: gradient 5s ease infinite;
          animation: gradient 5s ease infinite;
}

.card > img {
  margin: 30px;
}

.title > header {
  color: #fff;
  font-size: 2.5rem;
}

header > span {
  color: #0d6efd6e;
}

.cards {
  -webkit-animation: revealAnimation 1s forwards;
          animation: revealAnimation 1s forwards;
}

/* WIE */
.WIE {
  display: flex;
  font-family: monospace;
  flex-direction: column;
  align-items: center;
}
.WIE_MAIN {
  min-height: 100vh;
  background-size: 400% 400% !important;
  padding: 5rem 0 0.1rem 0;
  background: linear-gradient(-45deg, #99057b, #292975, #760599, #057b99);
  -webkit-animation: gradient 5s ease infinite;
          animation: gradient 5s ease infinite;
}
.wie_photo > img {
  width: 100%;
}
.wie_title {
  color: #fff;
  font-size: xx-large;
  text-align: center;
  font-weight: 500;
  font-family: "Borel", cursive;
}

.wie_desc,
.wie_media,
.about_footer {
  text-align: justify;
  padding: 1rem 2rem;
  color: #fff;
}

.about_footer {
  font-family: monospace;
  background: rgba(0, 0, 0, 0.25);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  border-radius: 10px;
}

@media only screen and (max-width: 1024px) {
  .about {
    padding-top: 10rem;
  }
  .card {
    width: auto;
  }
}
/* FOR DESKTOPS */
@media only screen and (min-width: 1024px) {
  .title > header {
    font-size: 3rem;
  }
  .WIE {
    flex-direction: row;
  }
  .wie_content {
    display: flex;
    flex-direction: column;
  }

  .wie_photo > img,
  .wie_title {
    width: 100%;
  }

  .wie_photo {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.card {
  min-height: 50vh;
  background: rgba(255, 255, 255, 0.25);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  border-radius: 10px;
  margin: 1rem;
  padding: 5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  font-family: monospace;
  -webkit-transform: translateY(50px);
          transform: translateY(50px);
  -webkit-animation: revealAnimation 1s forwards;
          animation: revealAnimation 1s forwards;
}
.icon > img {
  width: 100px;
  height: 100px;
  object-fit: contain;
}
.card / h3 {
  text-align: center;
}
/* Slider.css */
.slider {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(255 249 249 / 46%);
  padding: 1rem;
  box-sizing: border-box;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.slider > p {
  color: #000000a8;
  font-size: 1rem;
  font-weight: bolder;
}
.card:hover .slider {
  opacity: 1;
}
@media only screen and (max-width: 768px) {
  .card {
    width: 100%;
  }
}

